@media print {
  header, nav, footer {
    display: none;
  }
  body {
    font: 12px;
    line-height: 1.3;
  }
  section {
    padding: 20px 20px 40px 20px;
  }
  h3 {
    font-size: 2em;
    margin-bottom: .5em;
  }
  h4 { margin-bottom: 10px; }
  .about-me--p {
    margin: 0;
  }
  .about-me--link {
    display: none;
  }
  .healine-box {
    padding: 40px;
  }
  .headline {
    padding-top: 20px;
  }
  .offer--contents-points {
    padding: 20px;
  }
  .offer--contents li {
    margin-bottom: 10px;
  }
  .skills--contents {
    padding: 0;
    margin: 0;
  }
  .skills--logo {
    margin-bottom: 20px;
  }
  .skills--logo li {
    margin: .5em 1em;
  }
  .section-portfolio {
    padding-top: 0;
  }
  .section-portfolio h3 {
    margin-bottom: .5em;
  }
  .image--web {
    width: 300px;
  }
  .main-portfolio--btns {
    display: none;
  }
  .section-contact {
    display: none;
  }
}

@page {
  margin: .3cm .5cm;
}
